body {
  color: #000;
  font-family: Montserrat, sans-serif;
  font-size: 3.73vw;
  font-weight: 500;
  line-height: 1.12;
}

@media screen and (min-width: 768px) {
  body {
    font-size: 2.34vw;
  }
}

@media screen and (min-width: 1440px) {
  body {
    font-size: 1.67vw;
  }
}

@media screen and (max-width: 767px) {
  .body--not-scrollable {
    overflow: hidden;
  }
}

a {
  text-decoration: none;
  display: inline-block;
}

h1, h2, h3, p, ul, li {
  margin: 0;
  padding: 0;
}

ul {
  list-style: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.button {
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: 0;
  padding: 0;
  display: block;
}

.button:hover {
  box-shadow: none;
}

.button--pink {
  color: #fff;
  background-color: #fc7387;
  border-radius: 10.67vw;
  padding-top: 4vw;
  padding-bottom: 4vw;
  font-size: 4.27vw;
  font-weight: 600;
  box-shadow: 0 0 5.33vw 1.33vw rgba(252, 115, 135, .5);
}

@media screen and (min-width: 768px) {
  .button--pink {
    padding-top: 2.47vw;
    padding-bottom: 2.47vw;
    font-size: 2.34vw;
  }
}

@media screen and (min-width: 1440px) {
  .button--pink {
    padding-top: 1.32vw;
    padding-bottom: 1.32vw;
    font-size: 1.25vw;
  }
}

.section {
  position: relative;
  overflow: hidden;
}

.section__title {
  width: 62.66vw;
  height: 31.2vw;
  text-align: center;
  text-transform: uppercase;
  border-radius: 0 0 50% 50% / 0 0 100% 100%;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  font-size: 5.33vw;
  font-weight: 700;
  display: flex;
}

@media screen and (min-width: 768px) {
