.lang {
  width: 17vw;
  color: #0a0070;
  font-weight: 400;
  position: relative;
}

@media screen and (min-width: 768px) {
  .lang {
    width: 8.81vw;
  }
}

@media screen and (min-width: 1440px) {
  .lang {
    width: 5vw;
    font-size: 1.25vw;
  }
}

.lang--mobile {
  display: none;
}

@media screen and (max-width: 767px) {
  .lang--mobile {
    display: block;
    position: absolute;
    top: 12.8vw;
    left: 5.33vw;
  }
}

@media screen and (max-width: 767px) and (orientation: landscape) {
  .lang--mobile {
    top: .8vw;
    left: 1.33vw;
  }
}

.lang__content {
  z-index: 1;
  opacity: 0;
  width: 100%;
  background-color: #b8e2ed;
  transition: all .5s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 130%;
  left: 0;
  overflow: hidden;
}

.lang[data-state="active"] .lang__lable, .lang[data-state="active"] .lang__content {
  opacity: 1;
}

.lang[data-state="active"] .lang__lable {
  pointer-events: auto;
  max-height: 9.06vw;
}

@media screen and (min-width: 768px) {
  .lang[data-state="active"] .lang__lable {
    max-height: 4.42vw;
  }
}

@media screen and (min-width: 1440px) {
  .lang[data-state="active"] .lang__lable {
    max-height: 2.37vw;
  }
}

.lang__input {
  display: none;
}

.lang__title:after {
  content: "";
  width: 2.13vw;
  height: 1.06vw;
  background-image: url("caret.30ba8f91.svg");
  background-repeat: no-repeat;
  background-size: 2.13vw;
  margin-left: auto;
  transition: all .5s cubic-bezier(.4, 0, .2, 1);
  display: block;
}

@media screen and (min-width: 768px) {
  .lang__title:after {
    width: 1.04vw;
    height: .52vw;
    background-size: 1.04vw;
  }
}

@media screen and (min-width: 1440px) {
  .lang__title:after {
    width: .56vw;
    height: .28vw;
    background-size: .56vw;
  }
}

.lang[data-state="active"] .lang__title:after {
  transform: rotateX(180deg);
}

.lang__lable {
  pointer-events: none;
  max-height: 0;
  opacity: 0;
  cursor: pointer;
  padding-top: 2.13vw;
  padding-bottom: 2.13vw;
  padding-left: 2.13vw;
  transition: all .5s cubic-bezier(.4, 0, .2, 1);
  display: block;
}

@media screen and (min-width: 768px) {
  .lang__lable {
    padding-top: 1.04vw;
    padding-bottom: 1.04vw;
    padding-left: 1.04vw;
  }
}

@media screen and (min-width: 1440px) {
  .lang__lable {
    padding-top: .56vw;
    padding-bottom: .56vw;
    padding-left: .6vw;
  }
}

.lang__lable:nth-of-type(1):before {
  background-image: url("flag-ru.8ab2cc13.svg");
}

.lang__lable:nth-of-type(2):before {
  background-image: url("flag-ua.6f5f0f78.svg");
}

.lang__lable:nth-of-type(3):before {
  background-image: url("flag-en.60164d74.svg");
}

.lang__lable:nth-of-type(4):before {
  background-image: url("flag-pl.b4945f14.svg");
}

.lang__title, .lang__lable {
  align-items: center;
  display: flex;
  position: relative;
}

.lang__lable--hidden {
  display: none;
}

.lang__lable:before, .lang .lang__title:before {
  content: "";
  width: 4.53vw;
  height: 4.53vw;
  background-repeat: no-repeat;
  background-size: 4.27vw;
  margin-right: 1.06vw;
  display: block;
}

@media screen and (min-width: 768px) {
  .lang__lable:before, .lang .lang__title:before {
    width: 2.21vw;
    height: 2.21vw;
    background-size: 2.08vw;
    margin-right: .52vw;
  }
}

@media screen and (min-width: 1440px) {
  .lang__lable:before, .lang .lang__title:before {
    width: 1.18vw;
    height: 1.18vw;
    background-size: 1.11vw;
  }
}

.lang__title {
  cursor: pointer;
  padding-left: 2.13vw;
}

@media screen and (min-width: 768px) {
  .lang__title {
    padding-left: 1.04vw;
  }
}

@media screen and (min-width: 1440px) {
  .lang__title {
    padding-left: .56vw;
  }
}

.lang__title[data-lang="ru"]:before {
